import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { TeeSheetAdministrationSettingComponent } from './tee-sheet-administration-setting.component';


const routes: Routes = [
  {
    path: '',
    component: TeeSheetAdministrationSettingComponent,
    children: []
  },
  {
    path: 'setup-configuration',
    loadChildren: () => import('./pages/setup-configuration/setup-configuration.module').then(m => m.SetupConfigurationModule)
  },
  { path: '**', redirectTo: '' },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class TeeSheetAdministrationRoutingModule
{
}
