import { Component, Inject, OnInit } from '@angular/core';
import { AbstractControl, FormArray, FormControl, FormGroup, Validators } from '@angular/forms';
import { MatLegacyTableDataSource as MatTableDataSource } from '@angular/material/legacy-table';
import { TranslateService } from '@ngx-translate/core';
import { BreadCrumb, SnackBarService } from '@tei/material';
import { firstValueFrom } from 'rxjs';
import { ILotterySettingsService } from '../../../@intermediate/lottery-settings/lottery-settings.interface';
import { GetGroupSplitRulesSettingsRequest } from '../../../@intermediate/lottery-settings/models/get-group-split-rules.model';
import { GroupSplitRulesSettingForm, GroupSplitRulesSettingsForm, PostGroupSplitRulesSettingRequest } from '../../../@intermediate/lottery-settings/models/post-group-split-rules.model';

@Component({
  selector: "tei-tee-sheet-administration-lottery-group-split-rules-settings",
  templateUrl: "./group-split-rules-settings.component.html",
  styleUrls: ["./group-split-rules-settings.component.scss"],
})
export class GroupSplitRulesSettingsComponent implements OnInit
{
  displayedColumns: string[] = [
    "GroupNum",
    "SplitTee",
    "SingleTeeMinutes",
    "DoubleTeeMinutes",
  ];
  dataSource: MatTableDataSource<AbstractControl>;
  form: FormGroup<GroupSplitRulesSettingsForm>;
  breadCrumbData: BreadCrumb[];

  constructor(private translate: TranslateService,
    private snackBar: SnackBarService,
    @Inject("ILotterySettingsService") private lotterySettingsService: ILotterySettingsService
  ) { }

  async ngOnInit(): Promise<void>
  {
    this.setBreadCrumbData();
    await this.loadTableData();
  }

  async loadTableData(): Promise<void>
  {
    const request: GetGroupSplitRulesSettingsRequest = {
    };

    try
    {
      const response = await firstValueFrom(this.lotterySettingsService.GetGroupSplitRulesSettings(request));

      this.form = new FormGroup<GroupSplitRulesSettingsForm>({
        GroupSplitRulesSettingsList: new FormArray<FormGroup<GroupSplitRulesSettingForm>>(
          response.GroupSplitRulesSettingsList.map(setting => (new FormGroup<GroupSplitRulesSettingForm>(
            {
              GroupNum: new FormControl<number>(setting.GroupNum),
              SplitTee: new FormControl<boolean>(setting.SplitTee, [Validators.required]),
              SingleTeeMinutes: new FormControl<number>(setting.SingleTeeMinutes, [Validators.pattern(/^\d+$/), Validators.required]),
              DoubleTeeMinutes: new FormControl<number>(setting.DoubleTeeMinutes, [Validators.pattern(/^\d+$/), Validators.required])
            }
          )))
        )
      });

      this.dataSource = new MatTableDataSource(
        this.groupSplitRulesSettings.controls
      );
    } catch (error)
    {

    }
  }

  get groupSplitRulesSettings(): FormArray
  {
    return this.form.get("GroupSplitRulesSettingsList") as FormArray;
  }

  setBreadCrumbData(): void
  {
    this.breadCrumbData = [
      {
        title: this.translate.instant('TSADMINISTRATION.LOTTERY_REQUESTS.NAVIGATION.TEE_SHEET'),
        relativeUrl: '../../../'
      },
      {
        title: this.translate.instant('TSADMINISTRATION.NAVIGATION.SETUP_AND_CONFIGURATION'),
        relativeUrl: '../../'
      },
      {
        title: this.translate.instant('TSADMINISTRATION.NAVIGATION.LOTTERY_REQUESTS_SETUP'),
        relativeUrl: '../'
      },
      {
        title: this.translate.instant('TSADMINISTRATION.LOTTERY_REQUESTS.NAVIGATION.SPLIT_RULES'),
        relativeUrl: '',
      },
    ];
  }

  async save(): Promise<void>
  {
    const request: PostGroupSplitRulesSettingRequest = this.form.value as PostGroupSplitRulesSettingRequest;

    try
    {
      await firstValueFrom(this.lotterySettingsService.PostGroupSplitRulesSetting(request));

      this.snackBar.showSuccess(this.translate.instant('TSADMINISTRATION.LOTTERY_REQUESTS.SETTINGS.SETTINGS_SAVED'), "", null, "left");

    } catch (error)
    {

    }
  }
}
