import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { ILotterySettingsService } from '../@intermediate/lottery-settings/lottery-settings.interface';
import { DeleteCourseExemptionRequest, DeleteCourseExemptionResponse } from '../@intermediate/lottery-settings/models/delete-course-exemption.model';
import { GetCourseExemptionRequest, GetCourseExemptionResponse } from '../@intermediate/lottery-settings/models/get-course-exemption.model';
import { GetDesignationBasedSettingsRequest, GetDesignationBasedSettingsResponse } from '../@intermediate/lottery-settings/models/get-designation-based-settings.model';
import { GetGroupSplitRulesSettingsRequest, GetGroupSplitRulesSettingsResponse } from '../@intermediate/lottery-settings/models/get-group-split-rules.model';
import { GetLotterySettingsRequest, GetLotterySettingsResponse } from '../@intermediate/lottery-settings/models/get-lottery-settings.model';
import { GetWeightingSettingsRequest, GetWeightingSettingsResponse } from '../@intermediate/lottery-settings/models/get-lottery-weighting-settings.model';
import { PostCourseExemptionRequest, PostCourseExemptionResponse } from '../@intermediate/lottery-settings/models/post-course-exemption.model';
import { PostDesignationBasedSettingsRequest, PostDesignationBasedSettingsResponse } from '../@intermediate/lottery-settings/models/post-designation-based-settings.model';
import { PostGroupSplitRulesSettingRequest, PostGroupSplitRulesSettingResponse } from '../@intermediate/lottery-settings/models/post-group-split-rules.model';
import { PostLotterySettingsRequest, PostLotterySettingsResponse } from '../@intermediate/lottery-settings/models/post-lottery-settings.model';
import { PostWeightingSettingsRequest, PostWeightingSettingsResponse } from '../@intermediate/lottery-settings/models/post-lottery-weighting-settings.model';

@Injectable({
  providedIn: 'root'
})

export class LotterySettingsService implements ILotterySettingsService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + "/TShAdministration/LotterySettings";
  }

  public Get(request: GetLotterySettingsRequest): Observable<GetLotterySettingsResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetLotterySettingsResponse>(apiUrl, { params: request as any });
  }

  public Post(request: PostLotterySettingsRequest): Observable<PostLotterySettingsResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.post<PostLotterySettingsResponse>(apiUrl, request);
  }

  PostCourseExemption(request: PostCourseExemptionRequest): Observable<PostCourseExemptionResponse>
  {
    const apiUrl = this.serviceUrl + '/CourseExemptions';
    return this.http.post<PostCourseExemptionResponse>(apiUrl, request);
  }

  DeleteCourseExemption(request: DeleteCourseExemptionRequest): Observable<DeleteCourseExemptionResponse>
  {
    const apiUrl = this.serviceUrl + '/CourseExemptions';
    return this.http.delete<DeleteCourseExemptionResponse>(apiUrl, { params: request as any });
  }

  GetCourseExemption(request: GetCourseExemptionRequest): Observable<GetCourseExemptionResponse>
  {
    const apiUrl = this.serviceUrl + '/CourseExemptions';
    return this.http.get<GetCourseExemptionResponse>(apiUrl, { params: request as any });
  }

  GetDesignationBasedSettings(request: GetDesignationBasedSettingsRequest): Observable<GetDesignationBasedSettingsResponse>
  {
    const apiUrl = this.serviceUrl + '/DesignationSettings';
    return this.http.get<GetDesignationBasedSettingsResponse>(apiUrl, { params: request as any });
  }

  PostDesignationBasedSettings(request: PostDesignationBasedSettingsRequest): Observable<PostDesignationBasedSettingsResponse>
  {
    const apiUrl = this.serviceUrl + '/DesignationSettings';
    return this.http.post<PostDesignationBasedSettingsResponse>(apiUrl, request);
  }

  GetGroupSplitRulesSettings(request: GetGroupSplitRulesSettingsRequest): Observable<GetGroupSplitRulesSettingsResponse>
  {
    const apiUrl = this.serviceUrl + "/GroupSplitRulesSettings";
    return this.http.get<GetGroupSplitRulesSettingsResponse>(apiUrl, {
      params: request as any,
    });
  }
  PostGroupSplitRulesSetting(request: PostGroupSplitRulesSettingRequest): Observable<PostGroupSplitRulesSettingResponse>
  {
    const apiUrl = this.serviceUrl + "/GroupSplitRulesSettings";
    return this.http.post<PostGroupSplitRulesSettingResponse>(apiUrl, request);
  }

  GetWeightingSettings(request: GetWeightingSettingsRequest): Observable<GetWeightingSettingsResponse>
  {
    const apiUrl = this.serviceUrl + '/WeightingSettings';
    return this.http.get<GetWeightingSettingsResponse>(apiUrl, { params: request as any });
  }

  PostWeightingSettings(request: PostWeightingSettingsRequest): Observable<PostWeightingSettingsResponse>
  {
    const apiUrl = this.serviceUrl + '/WeightingSettings';
    return this.http.post<PostWeightingSettingsResponse>(apiUrl, request);
  }
}
