import { AfterViewInit, Component, Inject, OnInit } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog';
import { ActivatedRoute } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { ManageStandAloneLoadingService, ResponsiveService } from '@tei/core';
import
  {
    BreadCrumb,
    ImageUploaderComponent,
    ImageUploaderModel,
    SnackBarService
  } from '@tei/material';
import { firstValueFrom, Observable } from 'rxjs';
import { tap } from 'rxjs/operators';
import { ICoursesService } from '../../../@intermediate/courses/courses.interface';
import
  {
    AutomatedNotification,
    GetCourseGeneralSettingsRequest,
    GetCourseGeneralSettingsResponse,
  } from '../../../@intermediate/courses/model/get-course-general-settings.model';
import
  {
    CourseGeneralSettingsForm,
    PostCourseGeneralSettingsRequest,
  } from '../../../@intermediate/courses/model/post-course-general-settings.model';

@Component({
  selector: 'tei-tee-sheet-administration-courses-configuration-course-general',
  templateUrl: './course-general.component.html',
  styleUrls: ['./course-general.component.scss'],
})
export class CoursesConfigurationGeneralComponent
  implements OnInit, AfterViewInit
{
  form: FormGroup<CourseGeneralSettingsForm>;
  profilePhotoURL: string;
  AutomatedNotifications: AutomatedNotification[];
  breadCrumbData: BreadCrumb[] = [
    {
      title: this.translate.instant('TSADMINISTRATION.NAVIGATION.TEE_SHEET'),
      relativeUrl: '../../../../../',
    },
    {
      title: this.translate.instant('TSADMINISTRATION.NAVIGATION.SETUP_AND_CONFIGURATION'),
      relativeUrl: '../../../../'
    },
    {
      title: this.translate.instant('TSADMINISTRATION.NAVIGATION.COURSE_SETUP'),
      relativeUrl: '../../../'
    },
    {
      title: this.translate.instant('TSADMINISTRATION.NAVIGATION.COURSES'),
      relativeUrl: '../../',
    },
    {
      title: this.translate.instant('TSADMINISTRATION.COURSES.GENERAL.GENERAL'),
      relativeUrl: '',
    },
  ];
  course$: Observable<GetCourseGeneralSettingsResponse>;

  constructor(
    @Inject('ICoursesService') private coursesService: ICoursesService,
    private translate: TranslateService,
    public manageStandAloneLoadingService: ManageStandAloneLoadingService,
    private activatedRoute: ActivatedRoute,
    private snackbarService: SnackBarService,
    private responsiveService: ResponsiveService,
    private dialog: MatDialog
  ) { }

  ngOnInit(): void
  {
    const request: GetCourseGeneralSettingsRequest = {
      CourseID: this.activatedRoute.snapshot.paramMap.get('courseId'),
    };

    this.course$ = this.coursesService.GetGeneralSettings(request).pipe(
      tap((response) =>
      {
        this.AutomatedNotifications = response.AutomatedNotifications;
        this.form = new FormGroup<CourseGeneralSettingsForm>({
          CourseName: new FormControl<string>({
            value: response?.CourseName,
            disabled: true,
          }),
          CourseID: new FormControl<string>({
            value: response?.CourseID,
            disabled: true,
          }),
          Image: new FormControl<string>(''),
          AutomatedNotificationID: new FormControl<string>(response?.AutomatedNotificationID)
        });

        this.profilePhotoURL = response.ProfilePhoto;
      })
    );
  }

  ngAfterViewInit(): void
  {
    const screenSize = this.responsiveService.mediaBreakpoint.getValue();
    const isSmallScreen =
      screenSize === 'xs' || screenSize === 'sm' || screenSize === 'md';

    if (isSmallScreen)
    {
      setTimeout(() =>
      {
        this.setHeightByWindowInnerHeight();
      });
    }
  }

  setHeightByWindowInnerHeight(): void
  {
    const windowInnerHeight = window.innerHeight;
    const matDrawerContent = document.querySelector<HTMLElement>(
      '.mat-drawer-content'
    )!;
    if (matDrawerContent)
    {
      matDrawerContent.style.height = `${ windowInnerHeight - 56 }px`;
    }
  }

  openProfilePhotoDialog(): void
  {
    const imageFormats = '.jpg,.jpeg,.jpe,.jif,.jfif,.jfi,.png,.gif,.bmp,.dib,.tiff,.tif,.heif,.heic';
    const permittedFileTypes = ['JPG', 'JPEG', 'JPE', 'JFIF', 'PNG', 'GIF', 'BMP', 'TIFF', 'HEIC'];
    const data: ImageUploaderModel = {
      round: false,
      maintainAspectRatio: false,
      aspectRatio: null,
      acceptFormat: imageFormats,
      permittedFileTypes: permittedFileTypes,
      maxSize: 500,
      hideRemoveButton: true,
      imageServerUrl: this.profilePhotoURL,
    };

    const imageUploaderDialog = this.dialog.open(ImageUploaderComponent, {
      width: '400px',
      panelClass: ['o-dialog-md-fullscreen'],
      data,
    });

    imageUploaderDialog.afterClosed().subscribe((result) =>
    {
      if (result)
      {
        this.form.controls.Image.setValue(result.croppedImage);
        this.profilePhotoURL = result?.croppedImage;

        if (!result?.croppedImage)
        {
          this.form.controls.Image.setValue(null);
          this.profilePhotoURL = null;
        }
      }
    });
  }

  async save(): Promise<void>
  {
    if (this.form.invalid) return;

    try
    {
      const request: PostCourseGeneralSettingsRequest = {
        CourseID: this.form.getRawValue().CourseID,
        ProfilePhoto: this.form.value.Image,
        AutomatedNotificationID: this.form.value.AutomatedNotificationID
      };

      const formData = new FormData();
      formData.append("CourseID", request.CourseID);
      formData.append("AutomatedNotificationID", request.AutomatedNotificationID);

      if (request.ProfilePhoto)
      {
        let file: File;

        if (this.isFile(request.ProfilePhoto))
        {
          file = request.ProfilePhoto;
        } else if (typeof request.ProfilePhoto === 'string')
        {
          file = this.base64ToFile(request.ProfilePhoto);
        }

        if (file)
        {
          formData.append("ProfilePhoto", file);
        }
      }

      await firstValueFrom(this.coursesService.PostGeneralSettings(formData));

      this.snackbarService.showSuccess(
        this.translate.instant('TSADMINISTRATION.GENERAL.SUCCESS_MESSAGE'),
        '',
        null,
        'left'
      );
    } catch (error)
    {
    }
  }  
  
  private isFile(value: any): value is File
  {
    return value instanceof File || value instanceof Blob;
  }

  private base64ToFile(base64String: string): File
  {
    const arr = base64String.split(',');
    const mime = arr[0].match(/:(.*?);/)?.[1] || 'image/jpeg';
    const extension = mime.split('/')[1] || 'jpg';
    const fileName = `ProfilePhoto.${ extension }`;
    const bstr = atob(arr[1]);
    let n = bstr.length;
    const u8arr = new Uint8Array(n);

    while (n--)
    {
      u8arr[n] = bstr.charCodeAt(n);
    }

    return new File([u8arr], fileName, { type: mime });
  }
}
