import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

const routes: Routes = [
  { path: '', loadChildren: () => import('./lottery-settings/lottery-settings.module').then(m => m.LotterySettingsModule) },
  { path: 'designation-based-settings', loadChildren: () => import('./designation-based-settings/designation-based-settings.module').then(m => m.LotteryDesignationBasedSettingsModule) },
  { path: 'course-exemptions', loadChildren: () => import('./course-exemption/course-exemption.module').then(m => m.CourseExemptionModule) },
  { path: 'group-split-rules-settings', loadChildren: () => import('./group-split-rules-settings/group-split-rules-settings.module').then(m => m.GroupSplitRulesSettingsModule) },
  { path: 'weighting-settings', loadChildren: () => import('./weighting-settings/weighting-settings.module').then(m => m.WeightingSettingsModule) },
  { path: '**', redirectTo: '' },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class LotteryRequestsRoutingModule
{
}
