import { Component, Inject, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { TranslateService } from '@ngx-translate/core';
import { BreadCrumb, SnackBarService } from '@tei/material';
import { firstValueFrom } from 'rxjs';
import { ILotterySettingsService } from '../../../@intermediate/lottery-settings/lottery-settings.interface';
import { GetWeightingSettingsRequest } from '../../../@intermediate/lottery-settings/models/get-lottery-weighting-settings.model';
import { PostWeightingSettingsRequest, WeightingSettingsForm } from '../../../@intermediate/lottery-settings/models/post-lottery-weighting-settings.model';

@Component({
  selector: 'tei-tee-sheet-administration-lottery-weighting-settings',
  templateUrl: './weighting-settings.component.html',
  styleUrls: ['./weighting-settings.component.scss']
})
export class WeightingSettingsComponent implements OnInit
{
  breadCrumbData: BreadCrumb[] = [
    {
      title: this.translate.instant('TSADMINISTRATION.LOTTERY_REQUESTS.NAVIGATION.TEE_SHEET'),
      relativeUrl: '../../../'
    },
    {
      title: this.translate.instant('TSADMINISTRATION.NAVIGATION.SETUP_AND_CONFIGURATION'),
      relativeUrl: '../../'
    },
    {
      title: this.translate.instant('TSADMINISTRATION.NAVIGATION.LOTTERY_REQUESTS_SETUP'),
      relativeUrl: '../'
    },
    {
      title: this.translate.instant('TSADMINISTRATION.LOTTERY_REQUESTS.NAVIGATION.WEIGHTING'),
      relativeUrl: ''
    },
  ];
  form: FormGroup<WeightingSettingsForm>;

  constructor(
    private translate: TranslateService,
    private snackBar: SnackBarService,
    @Inject('ILotterySettingsService') private lotterySettingsService: ILotterySettingsService
  )
  {
  }

  async ngOnInit(): Promise<void>
  {
    const request: GetWeightingSettingsRequest =
    {
    };

    try
    {
      const response = await firstValueFrom(this.lotterySettingsService.GetWeightingSettings(request));

      this.form = new FormGroup<WeightingSettingsForm>({
        EnableNoShowWeighting: new FormControl<boolean>(response.EnableNoShowWeighting),
        WeightingTeeTimeDays: new FormControl<number>(response.WeightingTeeTimeDays, [Validators.required, Validators.pattern(/^\d+$/)]),
        WeightingHighPercentage: new FormControl<number>(response.WeightingHighPercentage, [Validators.required, Validators.min(0), Validators.max(1)]),
        WeightingLowPercentage: new FormControl<number>(response.WeightingLowPercentage, [Validators.required, Validators.min(0), Validators.max(1)])
      });
    } catch (error)
    {
    }
  }

  async save(): Promise<void>
  {
    try
    {
      await firstValueFrom(this.lotterySettingsService.PostWeightingSettings(this.form.value as PostWeightingSettingsRequest));

      this.snackBar.showSuccess(this.translate.instant('TSADMINISTRATION.LOTTERY_REQUESTS.SETTINGS.SETTINGS_SAVED'));

    } catch (error)
    {
    }
  }
}
