import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { SetupConfigurationComponent } from './setup-configuration.component';

const routes: Routes = [
    {
        path: '',
        component: SetupConfigurationComponent,
        children: []
    },
    {
        path: 'course-setup',
        loadChildren: () =>
            import('../course-setup/course-setup.module').then(m => m.CourseSetupModule),
    },
    {
        path: 'tee-time-web-booking-setup',
        loadChildren: () =>
            import('../web-settings/web-settings.module').then(m => m.WebSettingsModule),
    },
    {
        path: 'lottery-requests-setup',
        loadChildren: () =>
            import('../lottery-requests/lottery-requests.module').then(m => m.LotteryRequestsModule),
    },
    { path: '**', redirectTo: '' },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class SetupConfigurationRoutingModule
{
}