import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { RouterModule } from '@angular/router';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { BreadCrumbModule } from '@tei/material';
import { SecureNavigationModule } from '@tei/material-smart-components';
import { SetupConfigurationRoutingModule } from './setup-configuration-routing.module';
import { SetupConfigurationComponent } from './setup-configuration.component';

const BASE_MODULES = [CommonModule, FormsModule, ReactiveFormsModule, RouterModule, SetupConfigurationRoutingModule];

const TEI_MATERIAL_MODULES = [
  BreadCrumbModule,
];
const TEI_MATERIAL_SMART_COMPONENTS_MODULES = [
  SecureNavigationModule
];
const TEI_CORE_MODULES = [
  TeiTextTranslatePipeModule
];

const COMPONENTS = [SetupConfigurationComponent];

@NgModule({
  declarations: [...COMPONENTS],
  imports: [RouterModule.forChild([
    { path: '', component: SetupConfigurationComponent },

  ]),
    ...BASE_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...TEI_MATERIAL_SMART_COMPONENTS_MODULES,
    ...TEI_CORE_MODULES,
  ]
})
export class SetupConfigurationModule
{ }
