import { Component, Inject, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog';
import { TranslateService } from '@ngx-translate/core';
import { BreadCrumb, CardDialogComponent, ConfirmationDialogComponent } from '@tei/material';
import { firstValueFrom } from 'rxjs';
import { ILotterySettingsService } from '../../../@intermediate/lottery-settings/lottery-settings.interface';
import { DeleteCourseExemptionRequest } from '../../../@intermediate/lottery-settings/models/delete-course-exemption.model';
import { CourseExemption, GetCourseExemptionRequest, GetCourseExemptionResponse } from '../../../@intermediate/lottery-settings/models/get-course-exemption.model';
import { AddCourseExemptionDialogForm, PostCourseExemptionRequest } from '../../../@intermediate/lottery-settings/models/post-course-exemption.model';
import { AddCourseExemptionDialogComponent } from './add-course-exemption-dialog/add-course-exemption-dialog.component';

@Component({
  selector: 'tei-tee-sheet-administration-lottery-course-exemption',
  templateUrl: './course-exemption.component.html',
  styleUrls: ['./course-exemption.component.scss']
})
export class CourseExemptionComponent implements OnInit
{

  model: GetCourseExemptionResponse;
  displayedColumns: string[] = ['Course', 'Start', 'End', 'Action'];
  breadCrumbData: BreadCrumb[] = [
    {
      title: this.translate.instant('TSADMINISTRATION.LOTTERY_REQUESTS.NAVIGATION.TEE_SHEET'),
      relativeUrl: '../../../'
    },
    {
      title: this.translate.instant('TSADMINISTRATION.NAVIGATION.SETUP_AND_CONFIGURATION'),
      relativeUrl: '../../'
    },
    {
      title: this.translate.instant('TSADMINISTRATION.NAVIGATION.LOTTERY_REQUESTS_SETUP'),
      relativeUrl: '../'
    },
    {
      title: this.translate.instant('TSADMINISTRATION.LOTTERY_REQUESTS.NAVIGATION.COURSE_EXEMPTIONS'),
      relativeUrl: ''
    },
  ];
  constructor(
    private translate: TranslateService,
    public dialog: MatDialog,
    @Inject("ILotterySettingsService") private lotterySettingsService: ILotterySettingsService
  ) 
  {
  }

  async ngOnInit(): Promise<void>
  {
    await this.load();
  }

  private async load(): Promise<void>
  {
    const request: GetCourseExemptionRequest = {
    };

    try
    {
      this.model = await firstValueFrom(this.lotterySettingsService.GetCourseExemption(request));
    } catch (error)
    {
    }
  }

  async openNewCourseExemption(): Promise<void>
  {
    const dialogRef = this.dialog.open(AddCourseExemptionDialogComponent, {
      width: '300px',
      panelClass: ['o-dialog-sm-fullscreen', 'o-dialog-md-fullscreen'],
      data: this.model.LotteryCourses
    });


    const result = await firstValueFrom(dialogRef.afterClosed());

    if (result)
    {
      this.save(result);
    }
  }

  async save(form: FormGroup<AddCourseExemptionDialogForm>): Promise<void>
  {
    const request: PostCourseExemptionRequest = form.value as PostCourseExemptionRequest;

    try
    {
      await firstValueFrom(this.lotterySettingsService.PostCourseExemption(request));

      this.load();

    } catch (error)
    {

    }
  }

  async openDeleteCourseExemption(item: CourseExemption): Promise<void>
  {
    const dialogContent = {
      title: 'TSADMINISTRATION.LOTTERY_REQUESTS.COURSE_EXEMPTION.DELETE_EXEMPTION',
      content: this.translate.instant('TSADMINISTRATION.LOTTERY_REQUESTS.COURSE_EXEMPTION.DELETE_EXEMPTION_CONTENT'),
      cancelButtonLabel: 'Cancel',
      submitButtonLabel: 'TSADMINISTRATION.LOTTERY_REQUESTS.COURSE_EXEMPTION.DELETE',
      submitButtonColor: 'warn'
    };

    const dialogRef = this.dialog.open(CardDialogComponent, {
      panelClass: 'o-confirm-dialog',
      data: {
        component: ConfirmationDialogComponent,
        dialogData: dialogContent
      },
      width: '350px',
    });

    const result = await firstValueFrom(dialogRef.afterClosed());

    if (result)
    {
      this.deleteCourseExemption(item.ExemptionId);
    }
  }

  async deleteCourseExemption(exemptionId: string): Promise<void>
  {
    const request: DeleteCourseExemptionRequest = {
      ExemptionId: exemptionId
    };

    try
    {
      await firstValueFrom(this.lotterySettingsService.DeleteCourseExemption(request));

      this.load();

    } catch (error)
    {

    }
  }
}
