import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatLegacyButtonModule as MatButtonModule } from "@angular/material/legacy-button";
import { MatLegacyFormFieldModule as MatFormFieldModule } from "@angular/material/legacy-form-field";
import { MatLegacyInputModule as MatInputModule } from "@angular/material/legacy-input";
import { MatLegacySlideToggleModule as MatSlideToggleModule } from '@angular/material/legacy-slide-toggle';
import { RouterModule } from '@angular/router';
import { TranslateModule } from '@ngx-translate/core';
import { TeiDateTranslateModule, TeiTextTranslatePipeModule } from '@tei/core-translate';
import { FormlyCustomSettingCardWrapperModule, FormlyCustomSettingTextModule, FormlyCustomSettingToggleModule } from '@tei/formly-custom-components';
import { BreadCrumbModule, LoadingModule } from '@tei/material';
import { LotterySettingsService } from '../../../@services/lottery-settings.service';
import { WeightingSettingsComponent } from './weighting-settings.component';




const CORE_MODULES = [TeiTextTranslatePipeModule, TeiDateTranslateModule, TranslateModule];
const BASE_MODULES = [FormsModule, ReactiveFormsModule];
const MATERIAL_MODULES = [
  MatSlideToggleModule,
  MatButtonModule,
  MatFormFieldModule,
  MatInputModule
];
const TEI_MATERIAL_MODULES = [LoadingModule, FormlyCustomSettingToggleModule, FormlyCustomSettingCardWrapperModule, FormlyCustomSettingTextModule, BreadCrumbModule];

@NgModule({
  declarations: [WeightingSettingsComponent],
  imports: [
    RouterModule.forChild([{ path: '', component: WeightingSettingsComponent }]),
    ...CORE_MODULES,
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES
  ],
  providers: [{ provide: 'ILotterySettingsService', useClass: LotterySettingsService }],
})
export class WeightingSettingsModule { }
