import { Component, Inject, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { BreadCrumb, SnackBarService } from '@tei/material';
import { DesignationBasedSettingsModel } from '@tei/material-smart-components';
import { firstValueFrom, Observable, tap } from 'rxjs';
import { ILotterySettingsService } from '../../../@intermediate/lottery-settings/lottery-settings.interface';
import { GetDesignationBasedSettingsRequest, GetDesignationBasedSettingsResponse } from '../../../@intermediate/lottery-settings/models/get-designation-based-settings.model';
import { LotteryDesignationBasedSettingsForm, PostDesignationBasedSettingsRequest } from '../../../@intermediate/lottery-settings/models/post-designation-based-settings.model';

@Component({
  selector: 'tei-tee-sheet-administration-lottery-designation-based-settings',
  templateUrl: './designation-based-settings.component.html',
  styleUrls: ['./designation-based-settings.component.scss']
})
export class LotteryDesignationBasedSettingsComponent implements OnInit
{
  breadCrumbData: BreadCrumb[] = [
    {
      title: this.translate.instant('TSADMINISTRATION.LOTTERY_REQUESTS.NAVIGATION.TEE_SHEET'),
      relativeUrl: '../../../'
    },
    {
      title: this.translate.instant('TSADMINISTRATION.NAVIGATION.SETUP_AND_CONFIGURATION'),
      relativeUrl: '../../'
    },
    {
      title: this.translate.instant('TSADMINISTRATION.NAVIGATION.LOTTERY_REQUESTS_SETUP'),
      relativeUrl: '../'
    },
    {
      title: this.translate.instant('TSADMINISTRATION.LOTTERY_REQUESTS.NAVIGATION.DESIGNATION_SETTING'),
      relativeUrl: '',
    },
  ];

  lotteryDesignationBasedSettings$: Observable<GetDesignationBasedSettingsResponse>;
  form: FormGroup<LotteryDesignationBasedSettingsForm>;
  settings: DesignationBasedSettingsModel = {};

  constructor(
    @Inject('ILotterySettingsService') private lotterySettingsService: ILotterySettingsService,
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private translate: TranslateService,
    private snackBar: SnackBarService
  ) 
  {
  }

  ngOnInit(): void
  {
    const request: GetDesignationBasedSettingsRequest = {
    };

    this.lotteryDesignationBasedSettings$ = this.lotterySettingsService.GetDesignationBasedSettings(request).pipe(
      tap(response =>
      {
        this.settings = response.Settings;
        this.form = new FormGroup<LotteryDesignationBasedSettingsForm>({
          Settings: new FormGroup({})
        });
      })
    );
  }

  async save(): Promise<void>
  {
    try
    {
      const request: PostDesignationBasedSettingsRequest = {
        PermissionTable: this.form.value.Settings
      };

      await firstValueFrom(this.lotterySettingsService.PostDesignationBasedSettings(request));

      this.snackBar.showSuccess(this.translate.instant('TSADMINISTRATION.GENERAL.SUCCESS_MESSAGE'), '', null, 'left');

    } catch (error)
    {
    }
  }

  back(): void
  {
    this.router.navigate(['../'], { relativeTo: this.activatedRoute });
  }
}
