import { HttpClient } from "@angular/common/http";
import { Inject, Injectable } from "@angular/core";
import { IAPIBaseUrlProvider } from "@tei/core";
import { Observable } from "rxjs";
import { ICoursesService } from "../@intermediate/courses/courses.interface";
import { GetCourseGeneralSettingsRequest, GetCourseGeneralSettingsResponse } from "../@intermediate/courses/model/get-course-general-settings.model";
import { GetCourseWebSettingsRequest, GetCourseWebSettingsResponse } from "../@intermediate/courses/model/get-course-web-settings.model";
import { GetCoursesRequest, GetCoursesResponse } from "../@intermediate/courses/model/get-courses.model";
import { PostCourseGeneralSettingsResponse } from "../@intermediate/courses/model/post-course-general-settings.model";
import { PostCourseWebSettingsRequest, PostCourseWebSettingsResponse } from "../@intermediate/courses/model/post-course-web-settings.model";

@Injectable({
  providedIn: 'root'
})
export class CoursesService implements ICoursesService
{
  private readonly serviceUrl: string;

  constructor(@Inject("IAPIBaseUrlProvider") protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + "/TShAdministration/Courses";
  }

  Get(request: GetCoursesRequest): Observable<GetCoursesResponse>
  {
    const apiUrl = this.serviceUrl + "";

    return this.http.get<GetCoursesResponse>(apiUrl, { params: request as any });
  }

  GetGeneralSettings(request: GetCourseGeneralSettingsRequest): Observable<GetCourseGeneralSettingsResponse>
  {
    const apiUrl = this.serviceUrl + "/GeneralSettings";

    return this.http.get<GetCourseGeneralSettingsResponse>(apiUrl, { params: request as any });
  }

  PostGeneralSettings(request: FormData): Observable<PostCourseGeneralSettingsResponse>
  {
    const apiUrl = this.serviceUrl + "/GeneralSettings";

    return this.http.post<PostCourseGeneralSettingsResponse>(apiUrl, request);
  }

  GetWebSettings(request: GetCourseWebSettingsRequest): Observable<GetCourseWebSettingsResponse>
  {
    const apiUrl = this.serviceUrl + "/WebSettings";

    return this.http.get<GetCourseWebSettingsResponse>(apiUrl, { params: request as any });
  }

  PostWebSettings(request: PostCourseWebSettingsRequest): Observable<PostCourseWebSettingsResponse>
  {
    const apiUrl = this.serviceUrl + "/WebSettings";

    return this.http.post<PostCourseWebSettingsResponse>(apiUrl, request);
  }

}

