import { Component, Inject, OnInit, ViewChild } from '@angular/core';
import { MatDrawer } from '@angular/material/sidenav';
import { ActivatedRoute, Router } from '@angular/router';
import { ResponsiveService } from '@tei/core';
import { Observable } from 'rxjs';
import { ICoursesService } from '../../@intermediate/courses/courses.interface';
import { GetCourseGeneralSettingsRequest, GetCourseGeneralSettingsResponse } from '../../@intermediate/courses/model/get-course-general-settings.model';

@Component({
  selector: 'tei-office-course-right-side-action-bar-page-template',
  templateUrl: './right-side-action-bar-page-template.component.html',
  styleUrls: ['./right-side-action-bar-page-template.component.scss']
})
export class RightSideActionBarPageTemplateComponent implements OnInit
{
  @ViewChild('drawerRef', { static: true }) drawer: MatDrawer;
  isLoaded: boolean = false;
  course$: Observable<GetCourseGeneralSettingsResponse>;
  isSmallScreen: boolean;

  constructor(
    @Inject('ICoursesService') private coursesService: ICoursesService,
    private responsiveService: ResponsiveService,
    private router: Router,
    private activatedRoute: ActivatedRoute
  )
  {
  }

  ngOnInit(): void
  {
    this.isSmallScreen = this.responsiveService.screenWidth.getValue() <= 960;

    const request: GetCourseGeneralSettingsRequest = {
      CourseID: this.activatedRoute.snapshot.paramMap.get('courseId')
    };

    this.course$ = this.coursesService.GetGeneralSettings(request);
  }

  toggleDrawer(): void
  {
    this.drawer.toggle();
  }

  changeCourse(): void
  {
    this.router.navigate(['../../'], { relativeTo: this.activatedRoute });
  }
}
