import { Component, Inject, OnInit } from "@angular/core";
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog';
import { ActivatedRoute, Router } from "@angular/router";
import { TranslateService } from "@ngx-translate/core";
import { BreadCrumb } from "@tei/material";
import { ICoursesService } from "../../../@intermediate/courses/courses.interface";
import { Course, GetCoursesRequest } from "../../../@intermediate/courses/model/get-courses.model";

@Component({
  selector: "tei-tee-sheet-administration-courses-configuration-courses-table",
  templateUrl: "./courses-table.component.html",
  styleUrls: ["./courses-table.component.scss"]
})
export class CoursesConfigurationTableComponent implements OnInit
{
  breadCrumbData: BreadCrumb[] = [
    {
      title: this.translate.instant('TSADMINISTRATION.NAVIGATION.TEE_SHEET'),
      relativeUrl: '../../../'
    },
    {
      title: this.translate.instant('TSADMINISTRATION.NAVIGATION.SETUP_AND_CONFIGURATION'),
      relativeUrl: '../../'
    },
    {
      title: this.translate.instant('TSADMINISTRATION.NAVIGATION.COURSE_SETUP'),
      relativeUrl: '../'
    },
    {
      title: this.translate.instant('TSADMINISTRATION.NAVIGATION.COURSES'),
      relativeUrl: ''
    }
  ];
  courses: Course[];
  tableColumns: string[] = ["Course", "Actions"];

  constructor(
    @Inject("ICoursesService") private coursesService: ICoursesService,
    private dialog: MatDialog,
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private translate: TranslateService
  )
  {
  }

  ngOnInit(): void
  {
    this.load();
  }

  load(request?: GetCoursesRequest)
  {

    if (!request)
    {
      request = new GetCoursesRequest();
    }

    this.coursesService.Get(request)
      .pipe()
      .subscribe({
        next: (result) =>
        {
          this.courses = result.Courses;
        }
      });
  }

  onFiltersChange(filteredRequest: GetCoursesRequest)
  {
    this.load(filteredRequest);
  }

  goToEditCourse(course: Course): void
  {
    this.router.navigate(['edit', course.CourseID, 'general'], { relativeTo: this.activatedRoute });
  }
}
