import { DesignationBasedSettingsModel } from "@tei/material-smart-components";

export class GetCourseWebSettingsRequest
{
  CourseID: string;
}

export class GetCourseWebSettingsResponse
{
  NumberOfHoles: NumberOfHolesEnum;
  Settings: DesignationBasedSettingsModel;
}

export enum NumberOfHolesEnum
{
  EighteenHolesOnly,
  NineHolesOnly,
  NineOrEighteenHoles
}

export class PermissionTable
{
  Labels: string[];
  AllowBookingsOnCourse: Setting = new Setting();
  AllowSameDayBookings: Setting = new Setting();
  MinNumberOfPlayers: Setting = new Setting();
  MaxNumberOfPlayers: Setting = new Setting();
  AllowUsersToSelectTransportation: Setting = new Setting();
  EnablePowerCartSelection: Setting = new Setting();
  EnablePullCartSelection: Setting = new Setting();
  EnableWalkSelection: Setting = new Setting();
  DefaultSelectedTransportation: Setting = new Setting();
  CreditCardRequired: Setting = new Setting();
  ContactLookupTemplateID: Setting = new Setting();
  FieldGroupID: Setting = new Setting();
  AutomatedNotificationID: Setting = new Setting();
  DefaultPayMethod: Setting = new Setting();
  RequireSinglesToJoinGroups: Setting = new Setting();
}

export class Setting
{
  Options?: Option[];
  Id: string | number;
  SettingKeyName: string;
  SettingLabelName: string;
  ColumnKeyNames: string[];
  [key: string]: number | string | Option[] | string[] | boolean;
}

export interface Option
{
  label: string;
  value: string | number;
}