import { Component, Inject, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { TranslateService } from '@ngx-translate/core';
import { BreadCrumb, SnackBarService } from '@tei/material';
import moment from 'moment';
import { firstValueFrom, Observable } from 'rxjs';
import { tap } from 'rxjs/operators';
import { ILotterySettingsService } from '../../../@intermediate/lottery-settings/lottery-settings.interface';
import { GetLotterySettingsRequest, GetLotterySettingsResponse } from '../../../@intermediate/lottery-settings/models/get-lottery-settings.model';
import { LotterySettingsRequestForm, PostLotterySettingsRequest } from '../../../@intermediate/lottery-settings/models/post-lottery-settings.model';

@Component({
  selector: 'tei-tee-sheet-administration-lottery-settings',
  templateUrl: './lottery-settings.component.html',
  styleUrls: ['./lottery-settings.component.scss'],
})
export class LotterySettingsComponent implements OnInit
{

  constructor(
    private translate: TranslateService,
    @Inject('ILotterySettingsService') private lotterySettingsService: ILotterySettingsService,
    private snackBar: SnackBarService
  ) 
  {
  }

  model$: Observable<GetLotterySettingsResponse>;
  form: FormGroup<LotterySettingsRequestForm>;
  startTime: Date = new Date(1995, 11, 17, 0, 0, 0);
  endTime: Date = new Date(1995, 11, 17, 23, 45, 0);
  breadCrumbData: BreadCrumb[] = [
    {
      title: this.translate.instant('TSADMINISTRATION.LOTTERY_REQUESTS.NAVIGATION.TEE_SHEET'),
      relativeUrl: '../../'
    },
    {
      title: this.translate.instant('TSADMINISTRATION.NAVIGATION.SETUP_AND_CONFIGURATION'),
      relativeUrl: '../'
    },
    {
      title: this.translate.instant('TSADMINISTRATION.NAVIGATION.LOTTERY_REQUESTS_SETUP'),
      relativeUrl: ''
    },
  ];

  ngOnInit(): void
  {
    const request: GetLotterySettingsRequest = {
    };
    this.model$ = this.lotterySettingsService.Get(request)
      .pipe(
        tap((response) =>
        {
          this.form = new FormGroup<LotterySettingsRequestForm>({
            EnableLottery: new FormControl<boolean>(response.EnableLottery, [Validators.required]),
            DrawTime: new FormControl<Date>(moment(response.DrawTime ?? "00:00", "HH:mm").toDate(), [Validators.required]),
            LotteryRequestCutOffMins: new FormControl<number>(response.LotteryRequestCutOffMins, [Validators.pattern(/^\d+$/), Validators.required]),
            DaysOutFromPresentDay: new FormControl<number>(response.DaysOutFromPresentDay, [Validators.pattern(/^\d+$/), Validators.required]),
            DaysIncludedInLottery: new FormControl<number>(response.DaysIncludedInLottery, [Validators.pattern(/^\d+$/), Validators.required]),
            PickerStartTime: new FormControl<Date>(moment(response.PickerStartTime ?? "00:00", "HH:mm").toDate(), [Validators.required]),
            PickerEndTime: new FormControl<Date>(moment(response.PickerEndTime ?? "00:00", "HH:mm").toDate(), [Validators.required]),
            PickerIntervals: new FormControl<number>(response.PickerIntervals, [Validators.pattern(/^\d+$/), Validators.required]),
            EnableEarliestLatest: new FormControl<boolean>(response.EnableEarliestLatest),
            AllowNonConsecutiveBooking: new FormControl<boolean>(response.AllowNonConsecutiveBooking),
            TreatNonConsecutiveReqAsSeparate: new FormControl<boolean>(response.TreatNonConsecutiveReqAsSeparate),
          });
        })
      );
  }

  getFormControlByName(form: FormGroup<LotterySettingsRequestForm>, name: string): FormControl
  {
    return form.get(name) as FormControl;
  }

  async saveLotterySettings(): Promise<void>
  {
    try
    {
      const request: PostLotterySettingsRequest = {
        ...this.form.value,
        DrawTime: moment(this.form.value.DrawTime).format("HH:mm"),
        PickerStartTime: moment(this.form.value.PickerStartTime).format("HH:mm"),
        PickerEndTime: moment(this.form.value.PickerEndTime).format("HH:mm"),
      } as PostLotterySettingsRequest;

      await firstValueFrom(this.lotterySettingsService.Post(request));

      this.snackBar.showSuccess(this.translate.instant('TSADMINISTRATION.LOTTERY_REQUESTS.SETTINGS.SETTINGS_SAVED'), "", null, "left");

    } catch (error)
    {

    }
  }
}
