import { Component, Inject, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { BreadCrumb, SnackBarService } from '@tei/material';
import { DesignationBasedSettingsModel, DesignationBasedSettingsValidators } from '@tei/material-smart-components';
import { firstValueFrom, Observable } from 'rxjs';
import { tap } from 'rxjs/operators';
import { ICoursesService } from '../../../@intermediate/courses/courses.interface';
import { GetCourseWebSettingsRequest, GetCourseWebSettingsResponse, NumberOfHolesEnum } from '../../../@intermediate/courses/model/get-course-web-settings.model';
import { CourseWebSettingsForm, PostCourseWebSettingsRequest } from '../../../@intermediate/courses/model/post-course-web-settings.model';
@Component({
  selector: 'tei-tee-sheet-administration-courses-configuration-course-web-settings',
  templateUrl: './course-web-settings.component.html',
  styleUrls: ['./course-web-settings.component.scss']
})
export class CoursesConfigurationWebSettingsComponent implements OnInit
{
  settings: DesignationBasedSettingsModel = {};
  validators: DesignationBasedSettingsValidators = {
    Number: [Validators.required, Validators.min(1), Validators.max(5)]
  }
  form: FormGroup<CourseWebSettingsForm>;
  model: GetCourseWebSettingsResponse;
  breadCrumbData: BreadCrumb[] = [
    {
      title: this.translate.instant('TSADMINISTRATION.NAVIGATION.TEE_SHEET'),
      relativeUrl: '../../../../../'
    },
    {
      title: this.translate.instant('TSADMINISTRATION.NAVIGATION.SETUP_AND_CONFIGURATION'),
      relativeUrl: '../../../../'
    },
    {
      title: this.translate.instant('TSADMINISTRATION.NAVIGATION.COURSE_SETUP'),
      relativeUrl: '../../../'
    },
    {
      title: this.translate.instant('TSADMINISTRATION.NAVIGATION.COURSES'),
      relativeUrl: '../../'
    },
    {
      title: this.translate.instant('TSADMINISTRATION.COURSES.WEB_SETTINGS.WEB_SETTINGS'),
      relativeUrl: ''
    }
  ];
  course$: Observable<GetCourseWebSettingsResponse>;
  NumberOfHolesEnum = NumberOfHolesEnum;
  paymentInfo = {
    title: this.translate.instant('TSADMINISTRATION.WEB_SETTINGS.PAYMENT_OPTIONS.LABEL'),
    message: this.generatePaymentOptionsHTML(),
    textOkayButton: '',
    textCancelButton: this.translate.instant('TSADMINISTRATION.WEB_SETTINGS.PAYMENT_OPTIONS.CLOSE'),
    showOkayButton: false,
    fullScreenInMobileView: true,
    width: '600px'
  };
  constructor(
    @Inject('ICoursesService') private coursesService: ICoursesService,
    private router: Router,
    private translate: TranslateService,
    private snackbarService: SnackBarService,
    private activatedRoute: ActivatedRoute
  )
  {
  }

  ngOnInit(): void
  {
    const request: GetCourseWebSettingsRequest = {
      CourseID: this.activatedRoute.snapshot.paramMap.get('courseId')
    };

    this.course$ = this.coursesService.GetWebSettings(request).pipe(
      tap((response) =>
      {
        this.form = new FormGroup<CourseWebSettingsForm>({
          NumberOfHoles: new FormControl(response?.NumberOfHoles),
          Settings: new FormGroup({})
        });
        this.settings = response.Settings;
      })
    );
  }

  async save(): Promise<void>
  {
    try
    {
      const request: PostCourseWebSettingsRequest = {
        CourseID: this.activatedRoute.snapshot.paramMap.get('courseId'),
        NumberOfHoles: this.form.value.NumberOfHoles,
        PermissionTable: this.form.value.Settings as DesignationBasedSettingsModel
      };

      await firstValueFrom(this.coursesService.PostWebSettings(request));

      this.snackbarService.showSuccess(this.translate.instant('TSADMINISTRATION.GENERAL.SUCCESS_MESSAGE'), '', null, 'left');
    } catch (error)
    {

    }
  }

  generatePaymentOptionsHTML(): string
  {
    const options = [
      {
        label: this.translate.instant('TSADMINISTRATION.WEB_SETTINGS.PAYMENT_OPTIONS.LABEL_1.LABEL'),
        description: [
          this.translate.instant('TSADMINISTRATION.WEB_SETTINGS.PAYMENT_OPTIONS.LABEL_1.DISCRIPTION_1'),
          this.translate.instant('TSADMINISTRATION.WEB_SETTINGS.PAYMENT_OPTIONS.LABEL_1.DISCRIPTION_2')
        ]
      },
      {
        label: this.translate.instant('TSADMINISTRATION.WEB_SETTINGS.PAYMENT_OPTIONS.LABEL_2.LABEL'),
        description: [
          this.translate.instant('TSADMINISTRATION.WEB_SETTINGS.PAYMENT_OPTIONS.LABEL_2.DISCRIPTION_1'),
          this.translate.instant('TSADMINISTRATION.WEB_SETTINGS.PAYMENT_OPTIONS.LABEL_2.DISCRIPTION_2'),
          this.translate.instant('TSADMINISTRATION.WEB_SETTINGS.PAYMENT_OPTIONS.LABEL_2.DISCRIPTION_3')
        ]
      },
      {
        label: this.translate.instant('TSADMINISTRATION.WEB_SETTINGS.PAYMENT_OPTIONS.LABEL_3.LABEL'),
        description: [
          this.translate.instant('TSADMINISTRATION.WEB_SETTINGS.PAYMENT_OPTIONS.LABEL_3.DISCRIPTION_1')
        ]
      }
    ];

    return `
    <div>
      ${ options
        .map(
          option => `
            <div class="mb-3">
              <div class="a-text-primary mb-2">${ option.label }</div>
              <ul class="m-0 ps-3">
                ${ option.description
              .map(desc => `<li class="mt-1">${ desc }</li>`)
              .join('') }
              </ul>
            </div>
          `
        )
        .join('') }
    </div>
  `;
  }

  back(): void
  {
    this.router.navigate(['../'], { relativeTo: this.activatedRoute });
  }
}
