import { Component, EventEmitter, Output } from '@angular/core';
import { ChipType } from '@tei/material';
import { GetCoursesRequest } from '../../@intermediate/courses/model/get-courses.model';

@Component({
  selector: 'tei-tee-sheet-administration-courses-table-filter-header',
  templateUrl: './courses-table-filter-header.component.html',
  styleUrls: ['./courses-table-filter-header.component.scss']
})
export class CoursesTableFilterHeaderComponent
{
  @Output() enterFilterValueEvent = new EventEmitter<GetCoursesRequest>();

  ChipType = ChipType;
  name = '';

  enterFilterValue($event: Event): void
  {
    $event?.stopPropagation();
    $event?.preventDefault();
    this.onEmitEvent($event?.target['value']);
  }

  clickFilterValue(filterInput: HTMLInputElement): void
  {
    this.onEmitEvent(filterInput.value);
  }

  onEmitEvent(searchValue?: string): void
  {
    this.enterFilterValueEvent.emit(searchValue ? { SearchValue: searchValue } : {});
  }
}

