import { Component, Inject } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import moment from 'moment';
import { LotteryCourse } from '../../../../@intermediate/lottery-settings/models/get-course-exemption.model';
import { AddCourseExemptionDialogForm } from '../../../../@intermediate/lottery-settings/models/post-course-exemption.model';
@Component({
  selector: 'tei-booking-lottery-add-course-exemption-dialog',
  templateUrl: './add-course-exemption-dialog.component.html'
})
export class AddCourseExemptionDialogComponent
{
  constructor(
    @Inject(MAT_DIALOG_DATA) public data: LotteryCourse[],
    public dialog: MatDialogRef<AddCourseExemptionDialogComponent>)
  {
  }

  form = new FormGroup<AddCourseExemptionDialogForm>({
    CourseId: new FormControl(null, Validators.required),
    StartDate: new FormControl(this.getCurrentDate(), Validators.required),
    EndDate: new FormControl(this.getTomorrowDate(), Validators.required),
  });

  getCurrentDate()
  {
    return new Date();
  }

  getTomorrowDate()
  {
    const date = new Date();
    return moment(date).add(1, "days").toDate();
  }

  close()
  {
    this.dialog.close();
  }
}
