import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { CoursesConfigurationGeneralComponent } from '../courses/course-general/course-general.component';
import { CoursesConfigurationWebSettingsComponent } from '../courses/course-web-settings/course-web-settings.component';
import { CoursesConfigurationTableComponent } from '../courses/courses-table/courses-table.component';
import { CourseSetupComponent } from './course-setup.component';

const routes: Routes = [
    {
        path: '',
        component: CourseSetupComponent,
        children: []
    },
    { path: 'courses', component: CoursesConfigurationTableComponent },
    { path: 'courses/edit/:courseId/general', component: CoursesConfigurationGeneralComponent },
    { path: 'courses/edit/:courseId/web-settings', component: CoursesConfigurationWebSettingsComponent },
    { path: 'clubs', loadChildren: () => import('../clubs/clubs.module').then(m => m.ClubsModule) },
    { path: '**', redirectTo: '' },
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule],
})
export class CourseSetupRoutingModule
{
}
